import React from "react";
import { NavLink } from "react-router-dom";

export default function Surat(props) {
  return (
    <React.Fragment>
        <div className="my-3 capitalize ">
            <NavLink exact to={`/${props.nomor}`} className="text-center border grid grid-cols-5 hover:text-white hover:bg-purple-500 py-3">
                <p>{props.nomor}</p>
                <p className="text-2xl font-semibold">{props.asma}</p>
                <p>{props.arti}</p>
                <p>{props.ayat}</p>
                <p>{props.type}</p>
            </NavLink>
        </div>
    </React.Fragment>
  );
}
