import React from "react";
import quran from "../assets/quran.png";
import { NavLink } from "react-router-dom";

export default function Content() {
  return (
    <React.Fragment>
      <div className="grid grid-cols-8 py-16 px-20">
        <div className="col-span-4 mt-24">
          <h1 className="text-6xl mb-7 font-semibold">My Quran</h1>
          <p className="text-lg">
            Al-Qur'an, Qur'an, atau Quran, adalah sebuah kitab suci utama dalam
            agama Islam, yang umat Muslim percaya bahwa kitab ini diturunkan
            oleh Tuhan, kepada Nabi Muhammad. Kitab ini terbagi ke dalam
            beberapa surah dan setiap surahnya terbagi ke dalam beberapa ayat.
          </p>
          <div>
            {/* <button className="bg-purple-700 mt-6 shadow-md border hover:bg-white hover:text-purple-700 hover:border-purple-700 text-white py-4 px-6 text-xl rounded-full">go to surat</button> */}
          </div>
        </div>
        <img className="col-span-4" src={quran} alt="" />
      </div>
    </React.Fragment>
  );
}
