import axios from "axios";
import React from "react";
import { useParams } from "react-router-dom";

export default function Ayat(props) {
  const [artikels, setArtikels] = React.useState([]);
  const [nameSurat, setNameSurat] = React.useState([]);
  let { slug } = useParams();
  const getRespon = async () => {
    const url = `https://api.npoint.io/99c279bb173a6e28359c/surat/${slug}`;
    try {
      let respon = await axios.get(url);
      console.log(respon.data);
      console.log("sukses");
      setArtikels(respon.data);
    } catch (err) {
      console.log(err);
    }
  };

  

  React.useEffect(() => {
    getRespon();
  }, []);
  console.log({ slug });
  return (
    <React.Fragment>
      <div className="text-right px-24">
      {artikels?.map((ayat, index) => (
          <div key={index}>
              <p className="text-5xl my-10 text-gray-700">{ayat.ar}</p>
              <p className="text-3xl"><p className="font-semibold text-gray-800">Ayat {ayat.nomor}</p>{ayat.id}</p>
          </div>
        ))}
      </div>
    </React.Fragment>
  );
}