import axios from "axios";
import React from "react";
import Asma from "./asma";
import Surat from "./surat";

export default function List() {
  const [list, setList] = React.useState([]);
  const artikel = async () => {
    try {
      const url = `https://al-quran-8d642.firebaseio.com/data.json?print=pretty`;
      let respon = await axios.get(url);
      setList(respon.data);
      console.log(respon.data);
      console.log("sukses");
    } catch (err) {
      console.log(err);
    }
  };
  React.useEffect(() => {
    artikel();
  }, []);
  return (
    <React.Fragment>
      <h1 className="text-4xl font-semibold text-center capitalize">
        daftar surat
      </h1>
      <div className="grid grid-cols-5 capitalize text-center text-xl font-semibold mt-12">
          <p>no</p>
          <p>surat</p>
          <p>arti</p>
          <p>jumlah ayat</p>
          <p>jenis surat</p>
      </div>
      <div className="">
        {list.map((artikels, index) => (
          <Surat
            key={index}
            arti={artikels.arti}
            asma={artikels.asma}
            ayat={artikels.ayat}
            nomor={artikels.nomor}
            type={artikels.type}
          ></Surat>
        ))}
      </div>
    </React.Fragment>
  );
}
